nav {
  background-size: 100% 100%;
  width: 100%;
  height: 300px;
  position: relative;
  overflow: hidden; }
  nav img {
    width: 100%;
    height: 300px; }
  nav ul {
    list-style: none;
    height: 1.8rem;
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center; }
    nav ul li {
      display: inline-block;
      margin-right: 2rem; }
      nav ul li a {
        font-size: 1.8rem;
        height: 1.8rem;
        line-height: 1.8rem;
        text-decoration: none;
        display: block; }
      nav ul li a:hover {
        color: red; }
